body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.top-pane {
  background-color: hsla(224, 28%, 57%, 0.877);
}
.pane {
  height: 55vh;
  display: flex;
}

.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  background-color: hsl(180, 1%, 21%);
}

.editor-container.collapsed {
  flex-grow: 0;
}

/* To make the collapse same even when there is some content */
.editor-container.collapsed .CodeMirror-scroll {
  position: absolute;
  
}

.editor-title {
  display: flex;
  justify-content: space-between;
  background-color: hsl(225, 6%, 13%);
  color: #ffffff;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
}
.CodeMirror {
  height: 100% !important;
}

.code-mirror-wrapper {
  flex-grow: 1;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  overflow: hidden;
}

.expand-collapse-btn {
  margin-left: 0.5rem;
  background: none;
  border: none;
  color: rgb(255, 245, 245);
  cursor: pointer;
  outline: none;
}
.mainheader {
	background-color: #1e1f26;
	/* margin-top: -1.9%; */
	height: 7vh;
	display: flex;
	justify-content: space-between;
	flex-direction: row;
}
.right {
	margin-top: 0%;
	margin-left: 46%;
	display: flex;
	flex-direction: row;
	font-family: 'Lato', sans-serif;
	
}
.right .text h2 {
	font-weight: 700;
	overflow: hidden;
	color: #fff;
	margin-top: 7%;
	margin-left: 3%;
	font-size: 1.3rem;

	width: 200px;
    text-overflow: visible;
  
    vertical-align: middle;
}
.right .text h3 {
	font-weight: 400;
	margin-top: -5%;
	margin-left: 3%;
	width: 300px;

	font-size: 15px;
	color: #fff;
	
}
.buttons{
	margin-top: -15%;
}

.Topbutton1 {
	border: 3px solid #444857;
	background: #444857;
	padding-left: 8px;
    padding-right: 8px;
	font-size: 1rem;
	font-family: 'Lato', 'Lucida Grande', 'Lucida Sans Unicode', Tahoma, Sans-Serif;
    -webkit-appearance: none;
    font-size: 1rem;
    text-shadow: none;
    line-height: 1.2;
    display: inline-block;
    padding: 10px 16px;
    margin: 0px 10px 0px 0px;
    position: relative;
    border-radius: 4px;
    border: 3px solid transparent;
    background: #444857;
    color: #fff;
    cursor: pointer;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-decoration: none !important;
    text-align: center;
    font-weight: normal !important;
}